.header {
  width: 100%;
  height: 150px;
  background: #476275;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 0px 0px 40px 40px;
  font-family: Manrope;
  font-size: 18px;
  font-weight: 700;
  line-height: 24.59px;
  color: white;
  padding: 0 40px;
}

.header-left-side {
  display: flex;
  align-items: center;
}

.header-logo {
  color: white;
}

.header-links {
  display: flex;
  gap: 80px;
}

.header-right {
  display: flex;
  align-items: center;
}
