.sell_park-bg {
  position: absolute; /* Изменено на абсолютное позиционирование */
  width: 100%;
  z-index: -2;
}

.common_page_bg-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url("../../img/bg-news.svg");
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: -50px;
  height: 200px;
  z-index: -1;
}

.common_page_bg-container span {
  color: white;
  font-size: 40px;
  margin-top: 40px;
}

.common_page_main {
  display: flex;
  flex-direction: column;
}

.common_page_bg-blue-layer {
  position: absolute; /* Изменено на абсолютное позиционирование */
  width: 100%;
  height: 100%; /* Изменено на 100% */
  background: #476275;
  opacity: 0.6;
  z-index: -1; /* Поднято на уровень выше */
}

.sell_park-allow {
  width: 20px;
  height: 40px;
  z-index: 222;
}

.sell_park-items-container {
  display: flex;
  gap: 30px;
  justify-content: space-around;
  padding: 80px 0;
}

.sell_park-main-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.sell_park-img1 {
  background-image: url("../../img/sell_park1.png");
  width: 400px;
  height: 400px;
  background-size: contain;
  background-repeat: no-repeat;
}

.sell_park-img2 {
  background-image: url("../../img/sell_park2.png");
  width: 400px;
  height: 400px;
  background-size: contain;
  background-repeat: no-repeat;
}

.sell_park-img3 {
  background-image: url("../../img/sell_park3.png");
  width: 400px;
