.news_page-bg {
  position: absolute; /* Изменено на абсолютное позиционирование */
  width: 100%;
  z-index: -2;
}

.common_page_bg-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url("../../img/bg-news.svg");
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: -50px;
  height: 200px;
  z-index: -1;
}

.common_page_bg-container span {
  color: white;
  font-size: 40px;
  margin-top: 40px;
}

.common_page_main {
  display: flex;
  flex-direction: column;
}

.common_page_bg-blue-layer {
  position: absolute; /* Изменено на абсолютное позиционирование */
  width: 100%;
  height: 100%; /* Изменено на 100% */
  background: #476275;
  opacity: 0.6;
  z-index: -1; /* Поднято на уровень выше */
}

.news_page-allow {
  width: 20px;
  height: 40px;
  z-index: 222;
}

.partners_logo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
  padding-top: 40px;
}

.partners_logo-container-row {
  display: flex;
  gap: 30px;
  padding: 20px;
}

.parners_logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: #d9d9d9;
}

.partners_logo-container-detail {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
