.news_page-bg {
  position: absolute; /* Изменено на абсолютное позиционирование */
  width: 100%;
  z-index: -2;
}

.common_page_bg-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url("../../img/bg-partners.svg");
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: -50px;
  height: 200px;
  z-index: -1;
}

.common_page_bg-container span {
  color: white;
  font-size: 40px;
  margin-top: 40px;
}

.common_page_main {
  display: flex;
  flex-direction: column;
}

.common_page_bg-blue-layer {
  position: absolute; /* Изменено на абсолютное позиционирование */
  width: 100%;
  height: 100%; /* Изменено на 100% */
  background: #476275;
  opacity: 0.6;
  z-index: -1; /* Поднято на уровень выше */
}

.contacts_page-allow {
  width: 20px;
  height: 40px;
  z-index: 222;
}

.contacts_page-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 0;
}

.contacts_page-text-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px;
  width: 100%;
}

.contacts_page-text {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contacts_page-text span {
  width: 50%;
  font-size: 18px;
}

.contacts_img {
  background-image: url("../../img/contacts.svg");
  background-repeat: no-repeat;
  width: 250px;
  height: 300px;
}

.phone_icon {
  background-image: url("../../img/phone_icon.svg");
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
}
